@import url("https://fonts.googleapis.com/css?family=Montserrat:400,800");

* .Login {
  box-sizing: border-box;
}

body .Login {
  background: #e9e9eb;
  display: flex;
  font-size: medium;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  font-family: "Montserrat", sans-serif;
  height: 100vh;
}

.Login h1 {
  font-weight: bold;
  margin: 0;
}

.Login h2 {
  margin-top: -20px;
  margin-bottom: 40px;
  text-align: center;
}

.Login p {
  font-size: 14px;
  font-weight: 500;
  line-height: 20px;
  letter-spacing: 0.5px;
  margin: 20px 0 30px;
}

.Login span {
  font-size: 12px;
}

.Login a {
  color: #333;
  font-size: 14px;
  text-decoration: none;
  margin: 15px 0;
}

.Login button {
  cursor: pointer;
  border-radius: 20px;
  border: 1px solid #ff2558;
  background-color: #ff275a;
  color: #ffffff;
  font-size: 12px;
  font-weight: bold;
  padding: 12px 45px;
  margin-top: 10px;
  letter-spacing: 1px;
  text-transform: uppercase;
  transition: transform 80ms ease-in;
}

.Login .logout button {
  border: #3c97bf;
  position: absolute;
  top: 10px;
  right: 20px;
  background-color: #3c97bf;
}

.Login button:active {
  transform: scale(0.95);
}

.Login button:focus {
  outline: none;
}

.Login button.ghost {
  background-color: transparent;
  border-color: #ffffff;
}

.Login form {
  background-color: #ffffff;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 0 50px;
  height: 100%;
  text-align: center;
}

.Login input {
  background-color: #eee;
  border: none;
  padding: 12px 15px;
  margin: 8px 0;
  width: 100%;
}

#container {
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
  position: relative;
  overflow: hidden;
  width: 768px;
  max-width: 100%;
  min-height: 480px;
}

.Login .form-container {
  position: absolute;
  top: 0;
  height: 100%;
  transition: all 0.6s ease-in-out;
}

.Login .sign-in-container {
  left: 0;
  width: 50%;
  z-index: 2;
}

.Login .register-head {
  padding-bottom: 20px;
}

#container.right-panel-active .sign-in-container {
  transform: translateX(100%);
}

.Login .sign-up-container {
  left: 0;
  width: 50%;
  opacity: 0;
  z-index: 1;
}

#container.right-panel-active .sign-up-container {
  transform: translateX(100%);
  opacity: 1;
  z-index: 5;
  animation: show 0.6s;
}

@keyframes show {

  0%,
  49.99% {
    opacity: 0;
    z-index: 1;
  }

  50%,
  100% {
    opacity: 1;
    z-index: 5;
  }
}

#overlay-container {
  position: absolute;
  top: 0;
  left: 50%;
  width: 50%;
  height: 100%;
  overflow: hidden;
  transition: transform 0.6s ease-in-out;
  z-index: 100;
}

#container.right-panel-active .overlay-container {
  transform: translateX(-100%);
}

#overlay {
  background: #ff416c;
  background: -webkit-linear-gradient(to right, #ff4b2b, #ff416c);
  background: linear-gradient(to right, #ff4b2b, #ff416c);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 0 0;
  color: #ffffff;
  position: relative;
  left: -100%;
  height: 100%;
  width: 200%;
  transform: translateX(0);
  transition: transform 0.6s ease-in-out;
}

#container.right-panel-active .overlay {
  transform: translateX(50%);
}

.Login .overlay-panel {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 0 40px;
  text-align: center;
  top: 0;
  height: 100%;
  width: 50%;
  transform: translateX(0);
  transition: transform 0.6s ease-in-out;
}

.Login .overlay-left #overlay-left {
  transform: translateX(-20%);
}

#container.right-panel-active .overlay-left {
  transform: translateX(0);
}

.Login .overlay-right {
  right: 0;
  transform: translateX(0);
}

#container.right-panel-active .overlay-right {
  transform: translateX(20%);
}

.Login .social-container {
  margin: 20px 0;
}

.Login .social-container a {
  border: 1px solid #dddddd;
  border-radius: 50%;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  margin: 0 5px;
  height: 40px;
  width: 40px;
}

.Login footer {
  background-color: #222;
  color: #fff;
  font-size: 14px;
  bottom: 0;
  position: fixed;
  left: 0;
  right: 0;
  text-align: center;
  z-index: 999;
}

.Login footer p {
  margin: 10px 0;
}

.Login footer i {
  color: red;
}

.Login footer a {
  color: #3c97bf;
  text-decoration: none;
}

.Login .error-message {
  text-align: center;
  color: red;
}

.Login .success-message {
  text-align: center;
  color: rgb(93, 220, 8);
}